@import "vars";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 16px;
  scroll-behavior: smooth; }

body {
  background-color: $main;
  font-family: 'JetBrains Mono', monospace; }

a {
  text-decoration: none;
  color: inherit; }

ul {
  list-style-type: none; }

.transparent {
  opacity: .5; }
